.body {
    font-family: 'Montserrat', sans-serif;
    color: white;
    padding: 0 20px;
    margin: 0 auto;
    background-image: url("/src/pic/background.jpg");
    background-position: center;
    height: 100vh;
    background-size: cover;
  }

  .form__fieldset {
    border: 4px solid rgb(92, 158, 228);
    padding: 20px;
    border-radius: 20px;
    margin: 40px 0;
    max-width: 700px;
    margin: 0 auto;
  }

  .form__annotation {
    font-size: 11px;
    text-decoration: underline;
  }

  .form__title {
    text-align: center;
    font-size: 25px;
    background-color: rgb(92, 158, 228);
    padding: 5px;
    border: 3px solid black;
    border-radius: 10px;
  }